@keyframes ButtonGradientScrollBorder{
    0%{
      background-position:0% 50%;
      border-color: rgba(160,190,248,1);
    }
    8%{
      background-position:0% 50%;
      border-color: rgba(160,190,248,1);
    }
    50%{
      background-position:100% 50%;
      border-color: rgba(255,160,122,1);
    }
    100%{
      background-position:0% 50%;
      border-color: rgba(160,190,248,1);
    }
}
@keyframes ButtonGradientScroll{
    0%{
      background-position:100% 50%;
    }
    8%{
      background-position:100% 50%;
    }
    50%{
      background-position:0% 50%;
    }
    100%{
      background-position:100% 50%;
    }
}
@keyframes BGGradientScroll{
    0%{
      background-position:0% 50%;
    }
    50%{
      background-position:100% 50%;
    }
    100%{
      background-position:0% 50%;
    }
}


