.Image {
/*  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: row;*/
  flex-grow: 0;
  flex-shrink: 0;
  order: 2;
  position: relative;
  top: 0;
  left: 0;
  width: 90vh;
  max-width: 100vw;
  /*height: 90vh;*/
  /*max-height: 20em;*/
  /*border-style: solid;*/
  /*border-color: gray;*/
  margin: 0.5em;
}

.Image .Screen {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}
.Image .Content {
  position: absolute;
  top: 6%;
  left: 22%;
  width: 56%;
  z-index: 2;
}

.Image .Metapicture {
  position: absolute;
  top: 6%;
  left: 21.5%;
  width: 57%;
  height: 59.1%;
  z-index: 3;
  opacity: 0%;
  animation: test 6s ease infinite;
}
@keyframes test{
    0%{opacity: 0%;}
    40%{opacity: 0%;}
    50%{opacity: 100%;}
    90%{opacity: 100%;}
    100%{opacity: 0%;}
}



